import React from 'react';
import { Modal, Button, Row, Col } from 'react-bootstrap';

export default (props) => (
    <React.Fragment>
        <Modal.Header closeButton>
            <Modal.Title>Benefits for investors</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Row>
                <Col sm={6}>

                </Col>
                <Col sm={6}>

            <h4 className={'modal-title'}>Reduce risks </h4>
            <p>
             by lowering primary investment amounts to 10% to 20% of the token price, instead of the full cost.
                 <br /><br />
            </p>

            <h4 className={'modal-title'}>Increase investor ROI </h4>


                <p>
                  due to the opportunity to buy more tokens for the same amount of money.
                    <br /><br />
                </p>
            <h4 className={'modal-title'}>Motivate startups to focus on token rate</h4>
                <p>
                  cause their further investments depend on token price only. It also motivate startups to provide real evaluation (setting real strike price)
                <br />
                </p>


                </Col>
            </Row>
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={() => props.closeModal()}>Close</Button>
            </Modal.Footer>
    </React.Fragment>);


